ul.tabs {
  margin: 0;
  padding: 0;
  float: left;
  list-style: none;
  width: 100%;
  display: flex; }
ul.tabs li {
  font-size: 18px;
  float: left;
  margin: 0;
  cursor: pointer;
  margin-right: 30px;
  padding-bottom: 16px;
  color: $color-dark;
  position: relative; }
ul.tabs li:hover {
  color: $color-default;
  border-bottom: 1px solid $color-default; }
ul.tabs li.active {
  color: $color-default;
  display: block;
  border-bottom: 1px solid $color-default; }
ul.tabs li:last-child {
  margin-right: 0; }

.tab_container {
  border-top: none;
  clear: both;
  float: left;
  width: 100%;
  background: #fff;
  overflow: auto; }

.tab_content {
  padding-top: 52px;
  display: none; }
.tab_content p {
  font-weight: 300;
  font-size: 18px;
  line-height: 130%; }

.tab_drawer_heading {
  display: none; }

@media (max-width: $mobile-media){
  ul.tabs li {
    font-size: 12px;
    padding-bottom: 12px;
  }
  .tab_content {
    padding-top: 16px;
    p {
      font-size: 12px;
      line-height: normal;
    }
  }
}
@media screen and (max-width: 480px) {

  .tab_drawer_heading {
    color: #fff;
    margin: 0;
    padding: 5px 20px;
    display: block;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .d_active {
    color: #fff; } }

//tabs two
.tabs-all {
  &_lists {
    display: flex;
    justify-content: space-between;
  }
  .tab {
    display: inline-block;
    padding: 0 0 18px;
    cursor: pointer;
    position: relative;
    z-index:5;
    font-weight: 500;
    font-size: 24px;
    line-height: 120%;
    color: $color-dark;
  }
  .active {
    color: $color-blue;
    border-bottom: 1px solid $color-blue;
  }
  .tabContent {
    margin-top: 40px;
    position: relative;
    z-index:1;
  }
  .hide {
    display: none;
  }
  .show {
    display: block;
  }
}
